"use client";
import Link from "next/link";
import { useEffect, useState } from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";

export default function Home() {
  const [events, setEvents] = useState([]);
  const [roomName, setRoomName] = useState("");
  const [isCallStarted, setIsCallStarted] = useState(false);
  const [error, setError] = useState(null);
  const [jitsiLoaded, setJitsiLoaded] = useState(false);

  // Load Jitsi script
  useEffect(() => {
    if (!window.JitsiMeetExternalAPI) {
      const script = document.createElement("script");
      script.src = "https://meet.jit.si/external_api.js";
      script.async = true;
      script.onload = () => setJitsiLoaded(true);
      script.onerror = () => setError("Failed to load Jitsi script");
      document.body.appendChild(script);
      return () => document.body.removeChild(script);
    } else {
      setJitsiLoaded(true);
    }
  }, []);

  // Fetch events
  useEffect(() => {
    fetch("/api/events")
      .then((res) => {
        if (!res.ok) throw new Error("Failed to fetch events");
        return res.json();
      })
      .then((data) => setEvents(data))
      .catch((err) => setError(err.message));
  }, []);

  // Start Jitsi call
  useEffect(() => {
    if (isCallStarted && roomName && jitsiLoaded && window.JitsiMeetExternalAPI) {
      const domain = "meet.jit.si";
      const options = {
        roomName: roomName,
        parentNode: document.querySelector("#meet"),
        width: "100%",
        height: 500,
      };
      const api = new window.JitsiMeetExternalAPI(domain, options);
      return () => api.dispose();
    }
  }, [isCallStarted, roomName, jitsiLoaded]);

  // Initialize particles.js
  useEffect(() => {
    if (window.particlesJS) {
      window.particlesJS("particles-js", {
        particles: {
          number: { value: 80, density: { enable: true, value_area: 800 } },
          color: { value: "#00dbff" }, // Neon cyan particles
          shape: { type: "circle" },
          opacity: { value: 0.5, random: true },
          size: { value: 3, random: true },
          line_linked: {
            enable: true,
            distance: 150,
            color: "#00dbff",
            opacity: 0.4,
            width: 1,
          },
          move: {
            enable: true,
            speed: 2,
            direction: "none",
            random: false,
            straight: false,
            out_mode: "out",
            bounce: false,
          },
        },
        interactivity: {
          detect_on: "canvas",
          events: {
            onhover: { enable: true, mode: "repulse" },
            onclick: { enable: true, mode: "push" },
            resize: true,
          },
          modes: {
            repulse: { distance: 100, duration: 0.4 },
            push: { particles_nb: 4 },
          },
        },
        retina_detect: true,
      });
    }
  }, []);

  const handleDateClick = async (arg) => {
    const title = prompt("Enter event name:");
    if (title) {
      const newEvent = { title, date: arg.dateStr, votes: [] };
      try {
        const res = await fetch("/api/events", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(newEvent),
        });
        if (!res.ok) throw new Error("Failed to create event");
        const savedEvent = await res.json();
        setEvents([...events, savedEvent]);
      } catch (err) {
        setError(err.message);
      }
    }
  };

  const handleEventClick = async (info) => {
    const eventId = info.event.id;
    const userName = prompt("Enter your name to vote:");
    if (userName) {
      try {
        const res = await fetch(`/api/events/vote`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ eventId, userName }),
        });
        if (!res.ok) throw new Error("Failed to vote");
        const updatedEvents = await fetch("/api/events").then((res) => res.json());
        setEvents(updatedEvents);
      } catch (err) {
        setError(err.message);
      }
    }
  };

  const handleStartCall = () => {
    if (roomName.trim()) {
      if (!jitsiLoaded) {
        alert("Jitsi is still loading, please wait...");
      } else {
        setIsCallStarted(true);
      }
    } else {
      alert("Please enter a room name!");
    }
  };

  return (
    <div
    style={{
      minHeight: "100vh",
      background: "linear-gradient(135deg, #0a0a0a 0%, #1c2526 100%)",
      color: "#d4faff",
      fontFamily: "'Orbitron', 'Roboto', sans-serif",
      overflow: "auto",
      position: "relative",
    }}
  >
    {/* Header Section with Large Banner */}
    <header
      style={{
        textAlign: "center",
        padding: "100px 20px",
        backgroundImage: "url('/grandpa.jpg')", // Your grandpa's picture
        backgroundSize: "cover",
        backgroundPosition: "center",
        minHeight: "70vh", // Large banner size
        position: "relative",
        overflow: "hidden",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {/* Dark Overlay for Professional Look */}
      <div
        style={{
          position: "absolute",
          top: 0,
          left: 0,
          width: "100%",
          height: "100%",
          background: "linear-gradient(45deg, rgba(0, 20, 40, 0.8), rgba(0, 0, 0, 0.8))",
          zIndex: 0,
        }}
      />
      <h1
        style={{
          fontSize: "5rem", // Big, graceful title
          color: "#00dbff",
          fontWeight: 700,
          letterSpacing: "2px",
          textShadow: "0 0 20px rgba(0, 219, 255, 0.8), 0 0 40px rgba(0, 219, 255, 0.4)",
          marginBottom: "20px",
          position: "relative",
          zIndex: 1,
        }}
      >
        Cousin Chronicles
      </h1>
      <p
        style={{
          fontSize: "1.6rem",
          color: "#b0eaff",
          fontFamily: "'Roboto', sans-serif",
          maxWidth: "700px",
          margin: "0 auto 40px",
          textShadow: "0 0 10px rgba(0, 219, 255, 0.5)",
          position: "relative",
          zIndex: 1,
        }}
      >
        A quantum leap in family connectivity
      </p>
      <Link
        href="/public/images/foto (5).jpg"
        style={{
          display: "inline-block",
          padding: "15px 40px",
          fontSize: "1.3rem",
          color: "#ffffff",
          background: "linear-gradient(90deg, #00dbff, #ff00cc)",
          border: "2px solid #00dbff",
          borderRadius: "30px",
          textDecoration: "none",
          transition: "all 0.4s ease",
          boxShadow: "0 0 20px rgba(0, 219, 255, 0.6), 0 0 40px rgba(255, 0, 204, 0.4)",
          position: "relative",
          zIndex: 1,
          animation: "pulseGlow 2s infinite",
        }}
        onMouseOver={(e) => {
          e.target.style.transform = "scale(1.05)";
          e.target.style.boxShadow = "0 0 30px rgba(0, 219, 255, 0.8), 0 0 60px rgba(255, 0, 204, 0.6)";
        }}
        onMouseOut={(e) => {
          e.target.style.transform = "scale(1)";
          e.target.style.boxShadow = "0 0 20px rgba(0, 219, 255, 0.6), 0 0 40px rgba(255, 0, 204, 0.4)";
        }}
      >
        Enter the Nexus
      </Link>
    </header>
      {/* Main Content */}
      <main style={{ padding: "80px 20px", position: "relative", zIndex: 1 }}>
        {/* Scheduling Section */}
        <section
          style={{
            marginBottom: "100px",
            textAlign: "center",
            background: "rgba(10, 20, 30, 0.9)",
            borderRadius: "20px",
            padding: "50px",
            boxShadow: "0 10px 40px rgba(0, 219, 255, 0.3), 0 0 60px rgba(0, 0, 0, 0.7)",
            border: "1px solid rgba(0, 219, 255, 0.2)",
          }}
        >
          <h2
            style={{
              fontSize: "2.5rem",
              color: "#00dbff",
              fontWeight: 600,
              marginBottom: "25px",
              letterSpacing: "1.5px",
              textShadow: "0 0 15px rgba(0, 219, 255, 0.7)",
            }}
          >
            Temporal Scheduler
          </h2>
          {error && (
            <p
              style={{
                color: "#ff66cc",
                fontSize: "1.2rem",
                marginBottom: "25px",
                textShadow: "0 0 10px rgba(255, 102, 204, 0.5)",
              }}
            >
              Alert: {error}
            </p>
          )}
          <div style={{ maxWidth: "1000px", margin: "0 auto" }}>
            <FullCalendar
              plugins={[dayGridPlugin, interactionPlugin]}
              initialView="dayGridMonth"
              events={events.map((event) => ({
                title: `${event.title} (Votes: ${event.votes.length})`,
                date: event.date,
                id: event._id,
              }))}
              dateClick={handleDateClick}
              eventClick={handleEventClick}
              height="600px"
              style={{
                background: "rgba(255, 255, 255, 0.95)",
                borderRadius: "15px",
                boxShadow: "0 5px 25px rgba(0, 219, 255, 0.4)",
                border: "1px solid rgba(0, 219, 255, 0.3)",
              }}
            />
          </div>
        </section>

        {/* Video Call Section */}
        <section
          style={{
            textAlign: "center",
            background: "rgba(10, 20, 30, 0.9)",
            borderRadius: "20px",
            padding: "50px",
            boxShadow: "0 10px 40px rgba(0, 219, 255, 0.3), 0 0 60px rgba(0, 0, 0, 0.7)",
            border: "1px solid rgba(0, 219, 255, 0.2)",
          }}
        >
          <h2
            style={{
              fontSize: "2.5rem",
              color: "#00dbff",
              fontWeight: 600,
              marginBottom: "25px",
              letterSpacing: "1.5px",
              textShadow: "0 0 15px rgba(0, 219, 255, 0.7)",
            }}
          >
            Holo-Comm Portal
          </h2>
          {!isCallStarted ? (
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                gap: "20px",
                flexWrap: "wrap",
              }}
            >
              <input
                type="text"
                value={roomName}
                onChange={(e) => setRoomName(e.target.value)}
                placeholder="Enter portal code"
                style={{
                  padding: "15px 25px",
                  fontSize: "1.1rem",
                  width: "320px",
                  borderRadius: "30px",
                  border: "2px solid rgba(0, 219, 255, 0.4)",
                  background: "rgba(0, 20, 40, 0.8)",
                  color: "#d4faff",
                  outline: "none",
                  transition: "all 0.3s ease",
                  boxShadow: "0 0 15px rgba(0, 219, 255, 0.3)",
                }}
                onFocus={(e) => {
                  e.target.style.borderColor = "#00dbff";
                  e.target.style.boxShadow = "0 0 25px rgba(0, 219, 255, 0.6)";
                }}
                onBlur={(e) => {
                  e.target.style.borderColor = "rgba(0, 219, 255, 0.4)";
                  e.target.style.boxShadow = "0 0 15px rgba(0, 219, 255, 0.3)";
                }}
              />
              <button
                onClick={handleStartCall}
                style={{
                  padding: "15px 40px",
                  fontSize: "1.1rem",
                  background: "linear-gradient(90deg, #00dbff, #ff00cc)",
                  border: "2px solid #00dbff",
                  borderRadius: "30px",
                  color: "#ffffff",
                  cursor: "pointer",
                  transition: "all 0.4s ease",
                  boxShadow: "0 0 20px rgba(0, 219, 255, 0.6), 0 0 40px rgba(255, 0, 204, 0.4)",
                  animation: "pulseGlow 2s infinite",
                }}
                onMouseOver={(e) => {
                  e.target.style.transform = "scale(1.05)";
                  e.target.style.boxShadow = "0 0 30px rgba(0, 219, 255, 0.8), 0 0 60px rgba(255, 0, 204, 0.6)";
                }}
                onMouseOut={(e) => {
                  e.target.style.transform = "scale(1)";
                  e.target.style.boxShadow = "0 0 20px rgba(0, 219, 255, 0.6), 0 0 40px rgba(255, 0, 204, 0.4)";
                }}
              >
                Activate Portal
              </button>
            </div>
          ) : (
            <div
              id="meet"
              style={{
                marginTop: "25px",
                borderRadius: "15px",
                overflow: "hidden",
                boxShadow: "0 5px 25px rgba(0, 219, 255, 0.4)",
                border: "1px solid rgba(0, 219, 255, 0.3)",
              }}
            />
          )}
        </section>
      </main>

      {/* Inline CSS Animations */}
      <style jsx global>{`
        @keyframes holoShift {
          0% { transform: translateX(-100%) translateY(-100%); }
          100% { transform: translateX(100%) translateY(100%); }
        }
        @keyframes pulseGlow {
          0% { box-shadow: 0 0 20px rgba(0, 219, 255, 0.6), 0 0 40px rgba(255, 0, 204, 0.4); }
          50% { box-shadow: 0 0 30px rgba(0, 219, 255, 0.8), 0 0 50px rgba(255, 0, 204, 0.6); }
          100% { box-shadow: 0 0 20px rgba(0, 219, 255, 0.6), 0 0 40px rgba(255, 0, 204, 0.4); }
        }
      `}</style>
    </div>
  );
}